import axios from 'axios';
import store from '../state/state'
import Qs from 'qs'
// 
axios.interceptors.request.use(function (config) {
  // Do something before request is sent
  // return config;
//tokentokentokenkeytokenkey
    if (config.url && store.state.userLoginInfo != null){
      config.headers['X-uid'] = store.state.userLoginInfo.id
      config.headers['X-Token'] = store.state.userLoginInfo.token
    }
    return config;
}, function (error) {
  // Do something with request error
  return Promise.reject(error);
});
// 
axios.interceptors.response.use(function (response) {
// 
	console.log(response.data.status)
  if(!response.data.status){
    if(response.data.error_code == '10005'){
      store.state.__this.$notify.error({
        title: "",
        message: ''
      });
    	setTimeout(function(){store.state.__this.$router.replace({path: '/'})},2000)
    } else if(response.data.error_code == '20007') {
      // 
      store.state.__this.$notify.error({
        title: response.data.error_msg,
        message: ''
      });
      return response
    }
    else if(response.data.error_code == '60000') {
      // 
      store.state.__this.$notify.error({
        title: response.data.error_msg,
        message: ''
      });
      return response
    }
    else{
      store.state.__this.$message.error(response.data.error_msg);
    }
  }
  else{
    return response
  }
}, function (error) {
// 
  if (error && error.response) {
  	console.log(error.response.status)
        switch (error.response.status) {
            case 400: store.state.__this.$message.error('(400)') ; break;
            case 401: store.state.__this.$message.error('(401)'); break;
            case 403: store.state.__this.$message.error('(403)'); break;
            case 404: store.state.__this.$message.error('(404)'); break;
            case 408: store.state.__this.$message.error('(408)'); break;
            case 500: store.state.__this.$message.error('(500)'); break;
            case 501: store.state.__this.$message.error('(501)'); break;
            case 502: store.state.__this.$message.error('(502)'); break;
            case 503: store.state.__this.$message.error('(503)'); break;
            case 504: store.state.__this.$message.error('(504)'); break;
            case 505: store.state.__this.$message.error('HTTP(505)'); break;
            default: store.state.__this.$message.error('(${err.response.status})!');
        }
    }else{
        store.state.__this.$message.error('!')
    }
    //store.state.__this.$message.error(error.message);
return Promise.reject(error)
});
