export const getclassRoomList = (page, orgid,t) => axios.get('/index/classroom/index?time='+t, {
  params: { 'page': page, 'limit':'10','orgid':orgid}
})
// 
export const addclassRoom = (name,total,status,orgid) => axios.post('/index/classroom/add', {
  'name': name,
  'total':total,
  'status':status,
  'orgid':orgid
})
// 
export const delectclassRoom = (id,orgid) => axios.get('/index/classroom/del', {
  params: { 'id': id}
})
