<template>
    <!--  -->
    <div class='logins_conters'>
        <header class='logins_header'>
            <section class='_headersection'>
                <span class='login_head'></span>
                <span class='go_registered' @click.stop='ge_login'></span>
            </section>
        </header>
         <main class='logins_main'>
             <section class='login_input'>
                <label class='user input_label'>
                    <span class='input_label_user spacing'>
                        <em></em><em></em><em></em>
                    </span>
                    <el-input v-model="form.phone" autocomplete='off' placeholder=""></el-input>
                </label>
                <label class='input_label verification_code'>
                    <span class='input_label_user spacing'>
                      <em></em><em></em><em></em>
                    </span>
                    <el-input v-model="form.phoneCode" autocomplete='off' placeholder=""></el-input>
                    <el-button @click.stop='get_phoneCode' v-show='Time_s==0'></el-button>
                    <el-button @click.stop='get_phoneCode' v-show='Time_s > 0'>{{Time_s}}s</el-button>
                </label>
                <label class='input_label'>
                    <span class='input_label_user spacing'>
                     <em></em><em></em><em style='text-align: right;'></em>
                    </span>
                    <el-input v-model="form.passwords" autocomplete='off' placeholder="" :type='passwordText' @focus='burlInput(comThis)'></el-input>
                </label>
                <label class='input_label'>
                     <span class='input_label_user spacing'>
                        <em></em>
                        <em></em>
                        <em></em>
                        <em></em>
                    </span>
                    <el-input v-model="form.once_passwords" autocomplete='off' placeholder="" :type='passwordText' @focus='burlInput(comThis)'></el-input>
                </label>
                <label class='button_label'>
                  <el-button :loading="reginState" @click.stop='resetClick'></el-button>
                </label>
             </section>
         </main>
    </div>
</template>
<script>
import {getuserincode, resetPassword} from '../../../getserver/server'
import {setseSsionStorage} from '../../../cookie/cookie'
import {mapMutations} from 'vuex'
export default {
  name: 'resets',
  data () {
    return {
      Time_s: 0,
      reginState: false,
      comThis: this,
      passwordText: 'text',
      form : {
        phone: '',
        phoneCode: '', // 
        passwords: '', // 
        once_passwords: '' // 
      }
    }
  },
  methods: {
    ...mapMutations(['LOGIN_PAGE']),
    resetClick () {
      if (this.form.passwords != this.form.once_passwords) {
        this.$message.error('!')
        return
      } else if (this.form.phoneCode == '') {
        this.$message.error('!')
        return
      } else if (this.form.phone == '') {
        this.$message.error('!')
        return
      } 
      this.userReset()
    },
    async userReset(){
      this.reginState = true
      let reset = await resetPassword(this.form.phone, this.form.passwords, this.form.phoneCode, this.form.once_passwords)
      if (reset.data.status) {
        // 
        this.$notify({
          title: reset.data.sinfo,
          message: '',
          type: 'success',
          duration: 1500
        });
      } else {
        this.$notify.error({
          title: reset.data.error_msg,
          message: ''
        });
      }
      this.reginState = false
      this.form = {
        phone: '',
        phoneCode: '', // 
        passwords: '', // 
        once_passwords: '' // 
      }
    },
    get_phoneCode () {
      // 
      const phone_validation = /^1[385]\d{9}$/
      if (phone_validation.test(this.form.phone)){
        // 
        const that = this
        getuserincode(that.form.phone).then((res)=>{
          if (res.status) {
            // 
            that.Time_s = 60;//  60s
            that.$message({
              message: ',',
              type: 'success'
            });
            setTimeout(()=>{
              alert(':'+ res.data.sinfo)
            }, 1500);
            that.TimeServer()
          } else {
            that.$message.error('')
          }
        }).catch((error)=>{
          console.log(error);
        })
        return
      }
      // 
      this.$message.error('!');
    },
    ge_login () {
      // 
      this.tabLoginfun(1)
    },
    tabLoginfun (n) {
      setseSsionStorage('login_page', n)
      this.LOGIN_PAGE(n);
    },
    TimeServer () {
      // 
      const that = this
      let times =function(){
        setTimeout(()=>{
          that.Time_s--
          if (that.Time_s == 0 || that.Time_s == '0') {
            // 60s 
            clearTimeout(times)
            return;
          }
          times()
        }, 1000)
      }
      times()
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../style/mixin';
.logins_conters {
    @include wh(26%, 63%);
    min-height: 430px;
    background-color: #ffffff;
    margin-right: 15%;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    .logins_header{
      @include wh(100%, 13%);
      background: url("../../../../static/header_back/login_top_bg.png")no-repeat;
      background-size: 100% 100%;
      border-top-left-radius: 8px;
      border-top-right-radius: 8px;
      padding: 0 7.8%;
      box-sizing: border-box;
      ._headersection {
        @include wh(100%,100%);
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .login_head{
          font-size: 16px;
        }
      }
      .go_registered{
          font-size: 13px;
          display: flex;
          align-items: center;
          cursor: pointer;
          -moz-user-select:none; /**/
            -webkit-user-select:none; /*webkit*/
                -ms-user-select:none; /*IE10*/
                    -khtml-user-select:none; /**/
                        user-select:none;
        }
        .go_registered::after{
          content: '';
          display: inline-block;
          @include wh(15px,15px);
          background: url("../../../../static/login_icon/icon_switch.png")no-repeat;
          background-size: 100%;
          margin-left: 8px;
        }
    }
    .logins_main {
      flex: 1;
      padding: 7.7% 6.1%;
      box-sizing: border-box;
       .login_input {
          .input_label{
            margin-bottom: 5%;
          }
        .input_label{
          display: flex;
          align-items: center;
          font-size: 13px;
          color: #333333;
          .input_label_user{
            margin-right: 3.4%;
            @include wh(20%,auto);
            display: flex;
            justify-content: space-between;
            em{
              display: inline-block;
              @include wh(100%,100%);
              // letter-spacing: 10px;
            }
          }
          
        }
        .button_label{
            margin-top: 7.6%;
            display: inline-block;
            @include wh(100%, 100%);
        }
      }
    }
}
</style>
<style scoped>
.verification_code /deep/ .el-input{
    width: 40%;
    margin-right: 4%;  
}
.button_label /deep/ .el-button{
  width: 100%;
  height: 9.2%;
  background: #2769fb;
  color: #ffffff;
  font-size: 13px;
  border-radius: 8px;
}
.verification_code /deep/ .el-button{
  border: 1px solid #2769fb;
  color: #2769fb;
}
.input_label /deep/ .el-input{
    flex: 1;
}
</style>